<script>
import { Icon } from "@iconify/vue2";
import { mapActions } from 'vuex';

export default {
  name: 'ManageUsers',
  components: {
    Icon
  },
  data() {
    return {
      searchQuery: '',
      selectedRole: '',
      selectedStatus: '',
      showAddUserModal: false,
      showResetPasswordModal: false,
      showAssignRoleModal: false,
      selectedUser: null,
      newUser: {
        name: '',
        email: '',
        username: '',
        role: ''
      },
      passwordForm: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      assignRoleForm: {
        selectedRole: ''
      },
      availableRoles: [
        { value: 'user', label: 'User' },
        { value: 'moderator', label: 'Moderator' },
        { value: 'admin', label: 'Admin' },
      ],
      users: [],
      loading: false
    }
  },
  computed: {
    filteredUsers() {
      return this.users.filter(user => {
        const matchesSearch = !this.searchQuery || 
          user.name?.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          user.email?.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          user.username?.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          user.full_name?.toLowerCase().includes(this.searchQuery.toLowerCase());
        
        const matchesRole = !this.selectedRole || user.role === this.selectedRole;
        const matchesStatus = !this.selectedStatus || user.status === this.selectedStatus;
        
        return matchesSearch || matchesRole || matchesStatus;
      });
    }
  },
  mounted() {
    this.loadUsers();
  },
  methods: {
    ...mapActions(['fetchUsers', 'createUser', 'updateUser', 'deleteUser', 'toggleUserStatus', 'resetPassword', 'assignRoleToUser', 'removeRoleFromUser']),
    
    async loadUsers() {
      try {
        this.loading = true;
        const response = await this.$store.dispatch('fetchUsers');
        
        console.log('API Response:', response); // Debug log
        
        // Handle different response structures
        let userData = [];
        
        if (response && response.data) {
          // If response has data property
          if (Array.isArray(response.data)) {
            userData = response.data;
          } else if (response.data.users && Array.isArray(response.data.users)) {
            userData = response.data.users;
          } else if (response.data.data && Array.isArray(response.data.data)) {
            userData = response.data.data;
          }
        } else if (Array.isArray(response)) {
          // If response is directly an array
          userData = response;
        }
        
        // Transform the data to match our component structure
        this.users = userData.map(user => ({
          id: user.id,
          name: user.firstName || 'Unknown User',
          full_name: user.firstName+' '+user.lastName || 'Unknown User',
          email: user.email || '',
          username: user.username || '',
          roles: user.roles || 'Unknown Role',
          status: user.isEnabled,
          initials: this.generateInitials(user.firstName || 'Unknown User'),
          createdAt: user.created_at || user.createdAt || new Date().toISOString(),
          lastActivity: user.lastLogin || null
        }));
        
        console.log('Processed users:', this.users); // Debug log
        
      } catch (error) {
        console.error('Error loading users:', error);
        this.$message.error('Gagal memuat data pengguna');
        
        // Fallback to mock data
        this.users = [
          {
            id: 1,
            name: 'System Administrator',
            full_name: 'System Administrator',
            email: 'admin@example.com',
            username: 'admin',
            role: 'admin',
            status: 'active',
            initials: 'SA',
            createdAt: '2024-01-15',
            lastActivity: '2024-01-20T10:30:00'
          },
          {
            id: 2,
            name: 'Reporter User',
            full_name: 'Reporter User',
            email: 'reporter@example.com',
            username: 'reporter',
            role: 'reporter',
            status: 'active',
            initials: 'RU',
            createdAt: '2024-01-10',
            lastActivity: '2024-01-20T09:15:00'
          }
        ];
      } finally {
        this.loading = false;
      }
    },
    
    generateInitials(name) {
      if (!name) return 'UN';
      return name.split(' ').map(n => n[0]).join('').toUpperCase().substring(0, 2);
    },
    
    getRoleLabel(role) {
      const labels = {
        USER: 'User',
        MODERATOR: 'Moderator',
        ADMIN: 'Admin'
      };
      return labels[role] || role;
    },
    
    getRoleBadgeClass(role) {
      const classes = {
        USER: 'bg-green-100 text-green-800',
        MODERATOR: 'bg-blue-100 text-blue-800',
        ADMIN: 'bg-purple-100 text-purple-800'
      };
      return classes[role] || 'bg-gray-100 text-gray-800';
    },
    
    getStatusLabel(status) {
      const labels = {
        true: 'Active',
        false: 'Inactive',
        suspended: 'Disuspend',
        pending: 'Menunggu Verifikasi'
      };
      return labels[status] || status;
    },
    
    getStatusBadgeClass(status) {
      const classes = {
        true: 'bg-green-100 text-green-800',
        false: 'bg-red-100 text-red-800',
        suspended: 'bg-red-100 text-red-800',
        pending: 'bg-yellow-100 text-yellow-800'
      };
      return classes[status] || 'bg-gray-100 text-gray-800';
    },
    
    formatDate(dateString) {
      if (!dateString) return '-';
      return new Date(dateString).toLocaleDateString('id-ID', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
    },
    
    formatRelativeTime(dateString) {
      if (!dateString) return 'Belum pernah';
      
      const now = new Date();
      const date = new Date(dateString);
      const diffInHours = Math.floor((now - date) / (1000 * 60 * 60));
      
      if (diffInHours < 1) return 'Baru saja';
      if (diffInHours < 24) return `${diffInHours} jam lalu`;
      
      const diffInDays = Math.floor(diffInHours / 24);
      if (diffInDays < 7) return `${diffInDays} hari lalu`;
      
      return this.formatDate(dateString);
    },
    
    async addUser() {
      try {
        this.loading = true;
        const userData = {
          username: this.newUser.username,
          email: this.newUser.email,
          full_name: this.newUser.name,
          role: this.newUser.role,
          password: 'defaultpassword123' // You might want to generate or ask for password
        };
        
        console.log('Creating user with data:', userData); // Debug log
        
        const response = await this.$store.dispatch('createUser', userData);
        console.log('Create user response:', response); // Debug log
        
        this.$message.success('User berhasil ditambahkan');
        this.showAddUserModal = false;
        this.newUser = { name: '', email: '', username: '', role: '' };
        await this.loadUsers(); // Refresh the list
        
      } catch (error) {
        console.error('Error adding user:', error);
        
        // Handle different error types
        let errorMessage = 'Gagal menambahkan user';
        
        if (error.response && error.response.data && error.response.data.message) {
          errorMessage = error.response.data.message;
        } else if (error.message && error.message !== 'Invalid response format') {
          errorMessage = error.message;
        }
        
        this.$message.error(errorMessage);
      } finally {
        this.loading = false;
      }
    },
    
    editUser(user) {
      this.$message.info(`Edit user: ${user.name} - Fitur akan segera hadir`);
      // TODO: Implement edit functionality with modal
    },
    
    async toggleUserStatus(user) {
      try {
        const action = user.status === true ? 'menonaktifkan' : 'mengaktifkan';
        
        await this.$confirm(
          `Apakah Anda yakin ingin ${action} pengguna ${user.name}?`,
          'Konfirmasi',
          {
            confirmButtonText: 'Ya',
            cancelButtonText: 'Batal',
            type: 'warning'
          }
        );
        
        console.log(`Toggling user ${user.id} status via API`); // Debug log
        
        const response = await this.$store.dispatch('toggleUserStatus', user.id);
        
        console.log('Toggle status response:', response); // Debug log
        
        this.$message.success(`Status user ${user.name} berhasil diubah`);
        await this.loadUsers(); // Refresh the list
        
      } catch (error) {
        if (error !== 'cancel') {
          console.error('Error toggling user status:', error);
          
          let errorMessage = 'Gagal mengubah status user';
          if (error.response && error.response.data && error.response.data.message) {
            errorMessage = error.response.data.message;
          } else if (error.message && error.message !== 'Invalid response format') {
            errorMessage = error.message;
          }
          
          this.$message.error(errorMessage);
        }
      }
    },
    
    resetPassword(user) {
      this.selectedUser = user;
      this.passwordForm = {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      };
      this.showResetPasswordModal = true;
    },

    async submitPasswordReset() {
      try {
        // Validation
        if (!this.passwordForm.currentPassword) {
          this.$message.error('Password saat ini harus diisi');
          return;
        }
        
        if (!this.passwordForm.newPassword) {
          this.$message.error('Password baru harus diisi');
          return;
        }
        
        if (this.passwordForm.newPassword !== this.passwordForm.confirmPassword) {
          this.$message.error('Konfirmasi password tidak cocok');
          return;
        }
        
        if (this.passwordForm.newPassword.length < 6) {
          this.$message.error('Password baru minimal 6 karakter');
          return;
        }

        console.log(`Resetting password for user: ${this.selectedUser.name}`);

        const response = await this.$store.dispatch('resetPassword', {
          currentPassword: this.passwordForm.currentPassword,
          newPassword: this.passwordForm.newPassword,
          confirmPassword: this.passwordForm.confirmPassword
        });

        console.log('Password reset response:', response);

        this.$message.success(`Password untuk ${this.selectedUser.name} berhasil direset`);
        this.showResetPasswordModal = false;
        this.selectedUser = null;

      } catch (error) {
        console.error('Error resetting password:', error);
        
        let errorMessage = 'Gagal mereset password';
        if (error.response && error.response.data && error.response.data.message) {
          errorMessage = error.response.data.message;
        } else if (error.message && error.message !== 'Invalid response format') {
          errorMessage = error.message;
        }
        
        this.$message.error(errorMessage);
      }
    },

    closeResetPasswordModal() {
      this.showResetPasswordModal = false;
      this.selectedUser = null;
      this.passwordForm = {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      };
    },

    assignRole(user) {
      this.selectedUser = user;
      this.assignRoleForm = {
        selectedRole: user.role // Set current role as default
      };
      this.showAssignRoleModal = true;
    },

    async submitRoleAssignment() {
      try {
        if (!this.assignRoleForm.selectedRole) {
          this.$message.error('Role harus dipilih');
          return;
        }

        if (this.assignRoleForm.selectedRole === this.selectedUser.role) {
          this.$message.warning('Role yang dipilih sama dengan role saat ini');
          return;
        }

        console.log(`Assigning role ${this.assignRoleForm.selectedRole} to user: ${this.selectedUser.name}`);

        const response = await this.$store.dispatch('assignRoleToUser', {
          userId: this.selectedUser.id,
          roleName: this.assignRoleForm.selectedRole
        });

        console.log('Role assignment response:', response);

        this.$message.success(`Role ${this.getRoleLabel(this.assignRoleForm.selectedRole)} berhasil diberikan kepada ${this.selectedUser.name}`);
        this.showAssignRoleModal = false;
        this.selectedUser = null;
        await this.loadUsers(); // Refresh the list

      } catch (error) {
        console.error('Error assigning role:', error);
        
        let errorMessage = 'Gagal mengubah role user';
        if (error.response && error.response.data && error.response.data.message) {
          errorMessage = error.response.data.message;
        } else if (error.message && error.message !== 'Invalid response format') {
          errorMessage = error.message;
        }
        
        this.$message.error(errorMessage);
      }
    },

    closeAssignRoleModal() {
      this.showAssignRoleModal = false;
      this.selectedUser = null;
      this.assignRoleForm = {
        selectedRole: ''
      };
    },
    
    async deleteUser(user) {
      try {
        await this.$confirm(
          `Apakah Anda yakin ingin menghapus user ${user.name}? Tindakan ini tidak dapat dibatalkan.`,
          'Konfirmasi Hapus',
          {
            confirmButtonText: 'Hapus',
            cancelButtonText: 'Batal',
            type: 'error'
          }
        );
        
        console.log(`Deleting user:`, user.id); // Debug log
        
        const response = await this.$store.dispatch('deleteUser', user.id);
        console.log('Delete user response:', response); // Debug log
        
        this.$message.success('User berhasil dihapus');
        await this.loadUsers(); // Refresh the list
        
      } catch (error) {
        if (error !== 'cancel') {
          console.error('Error deleting user:', error);
          
          let errorMessage = 'Gagal menghapus user';
          if (error.response && error.response.data && error.response.data.message) {
            errorMessage = error.response.data.message;
          } else if (error.message && error.message !== 'Invalid response format') {
            errorMessage = error.message;
          }
          
          this.$message.error(errorMessage);
        }
      }
    },

    async removeRole(user) {
      try {
        // Check if user has roles array and has more than just USER role
        if (!user.roles || !Array.isArray(user.roles) || user.roles.length === 0) {
          this.$message.warning('User ini tidak memiliki role yang dapat dihapus');
          return;
        }

        // Filter out USER role as it's the minimum role
        const removableRoles = user.roles.filter(role => role !== 'USER');
        
        if (removableRoles.length === 0) {
          this.$message.warning('User ini hanya memiliki role minimum (USER) yang tidak dapat dihapus');
          return;
        }

        // If user has only one removable role, remove it directly
        if (removableRoles.length === 1) {
          const roleToRemove = removableRoles[0];
          
          await this.$confirm(
            `Apakah Anda yakin ingin menghapus role ${this.getRoleLabel(roleToRemove)} dari user ${user.name}?`,
            'Konfirmasi Hapus Role',
            {
              confirmButtonText: 'Hapus Role',
              cancelButtonText: 'Batal',
              type: 'warning'
            }
          );

          console.log(`Removing role ${roleToRemove} from user: ${user.name}`);

          const response = await this.$store.dispatch('removeRoleFromUser', {
            userId: user.id,
            roleName: roleToRemove
          });

          console.log('Role removal response:', response);

          this.$message.success(`Role ${this.getRoleLabel(roleToRemove)} berhasil dihapus dari ${user.name}`);
          await this.loadUsers(); // Refresh the list
          
        } else {
          // If user has multiple removable roles, show selection dialog
          const { value: selectedRole } = await this.$prompt(
            `User ${user.name} memiliki beberapa role. Pilih role yang ingin dihapus:\n\n` +
            removableRoles.map((role, index) => `${index + 1}. ${this.getRoleLabel(role)}`).join('\n') +
            '\n\nMasukkan nomor role (1-' + removableRoles.length + '):',
            'Pilih Role untuk Dihapus',
            {
              confirmButtonText: 'Hapus Role',
              cancelButtonText: 'Batal',
              inputPattern: new RegExp(`^[1-${removableRoles.length}]$`),
              inputErrorMessage: `Masukkan nomor antara 1-${removableRoles.length}`
            }
          );

          const roleIndex = parseInt(selectedRole) - 1;
          const roleToRemove = removableRoles[roleIndex];

          console.log(`Removing role ${roleToRemove} from user: ${user.name}`);

          const response = await this.$store.dispatch('removeRoleFromUser', {
            userId: user.id,
            roleName: roleToRemove
          });

          console.log('Role removal response:', response);

          this.$message.success(`Role ${this.getRoleLabel(roleToRemove)} berhasil dihapus dari ${user.name}`);
          await this.loadUsers(); // Refresh the list
        }

      } catch (error) {
        if (error !== 'cancel') {
          console.error('Error removing role:', error);
          
          let errorMessage = 'Gagal menghapus role user';
          if (error.response && error.response.data && error.response.data.message) {
            errorMessage = error.response.data.message;
          } else if (error.message && error.message !== 'Invalid response format') {
            errorMessage = error.message;
          }
          
          this.$message.error(errorMessage);
        }
      }
    }
  }
}
</script>

